export default {
    data() {
        return {
            
        }
    },
    components: {
        
    },
    computed: {
        user(){
            if(this.$store.state.user == '') this.$store.commit('USER')
            return this.$store.state.user
        }
    },
    created(){
        document.title = '业主认证'
    },
    methods: {
        
    },
}



